___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 650px) {
  .navbar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }

  .navbar .navbar-brand img {
    height: 50px;
  }

  .navbar .navbar-menu {
    display: none;
  }

  .navbar .navbar-navigation {
    position: static;
    width: 100%;
    background-color: transparent;
  }

  .navbar .navbar-navigation li{
    display: inline-block;
  }  
  
  .navbar .navbar-navigation li a{
    color: white;
  }

  .page-title .title {
    font-size: 30px;
  }

  /* welcome */
  .welcome-message h1 {
    font-weight: 1000;
    font-size: 40px;
    text-stroke: 1px white;
    -webkit-text-stroke: 1px white;
    margin: 15px auto;
  }
  
  .welcome-message .caption{
    color: white;
    font-size: 20px;
    line-height: 25px;
    max-width: 800px;
    margin: 0 auto;
  }

  /* main */
  .restos {
    grid-template-columns: 1fr 1fr;
  }

  .resto {
    grid-template-columns: auto 1fr;
  }

  .resto .resto__name {
    grid-column-start: 1;
    grid-column-end: 3;
  }

  .resto .resto-info {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  .place-container .place-option {
    flex-basis: calc(33.33% - 10px); /* Mengubah flex-basis untuk layar lebih besar */
  }
}

@media screen and (min-width: 800px) {
  .navbar .navbar-navigation {
    font-size: 18px;
  }
}

@media screen and (min-width: 850px) {
  .restos {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1200px) {
  .restos {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (min-width: 1600px) {
  .restos {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media screen and (max-width: 200px) {
  body *{
    padding: 0;
    margin: 0;
  }
  
  h1{
    font-size: 10vw; 
  }

  p{
    font-size: 6vw;
  }
  .navbar .navbar-menu button {
    background-color: transparent;
    border: none;
    font-size: 10vw;
    padding: 0px;
    cursor: pointer;
    color: white;
  }

  .content{
    padding: 0; 
  }

  .resto .resto-menu .food-drink{
    display: block;
  }

  .resto .resto-menu .food-drink .food, .resto .resto-menu .food-drink .drink{
    width: 100%;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,+BAA+B;IAC/B,iBAAiB;EACnB;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,gBAAgB;IAChB,WAAW;IACX,6BAA6B;EAC/B;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,eAAe;EACjB;;EAEA,YAAY;EACZ;IACE,iBAAiB;IACjB,eAAe;IACf,sBAAsB;IACtB,8BAA8B;IAC9B,iBAAiB;EACnB;;EAEA;IACE,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;EAChB;;EAEA,SAAS;EACT;IACE,8BAA8B;EAChC;;EAEA;IACE,+BAA+B;EACjC;;EAEA;IACE,oBAAoB;IACpB,kBAAkB;EACpB;;EAEA;IACE,oBAAoB;IACpB,kBAAkB;EACpB;EACA;IACE,+BAA+B,EAAE,gDAAgD;EACnF;AACF;;AAEA;EACE;IACE,eAAe;EACjB;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,UAAU;IACV,SAAS;EACX;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,cAAc;EAChB;EACA;IACE,6BAA6B;IAC7B,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,eAAe;IACf,YAAY;EACd;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,WAAW;EACb;AACF","sourcesContent":["@media screen and (min-width: 650px) {\r\n  .navbar {\r\n    grid-template-columns: 1fr auto;\r\n    padding: 8px 32px;\r\n  }\r\n\r\n  .navbar .navbar-brand img {\r\n    height: 50px;\r\n  }\r\n\r\n  .navbar .navbar-menu {\r\n    display: none;\r\n  }\r\n\r\n  .navbar .navbar-navigation {\r\n    position: static;\r\n    width: 100%;\r\n    background-color: transparent;\r\n  }\r\n\r\n  .navbar .navbar-navigation li{\r\n    display: inline-block;\r\n  }  \r\n  \r\n  .navbar .navbar-navigation li a{\r\n    color: white;\r\n  }\r\n\r\n  .page-title .title {\r\n    font-size: 30px;\r\n  }\r\n\r\n  /* welcome */\r\n  .welcome-message h1 {\r\n    font-weight: 1000;\r\n    font-size: 40px;\r\n    text-stroke: 1px white;\r\n    -webkit-text-stroke: 1px white;\r\n    margin: 15px auto;\r\n  }\r\n  \r\n  .welcome-message .caption{\r\n    color: white;\r\n    font-size: 20px;\r\n    line-height: 25px;\r\n    max-width: 800px;\r\n    margin: 0 auto;\r\n  }\r\n\r\n  /* main */\r\n  .restos {\r\n    grid-te